import React from 'react';
import { KLayout } from "./k-layout";
import { Row, Col } from 'antd';

/**
 * Header 
 * @param {string} bgImg 
 * @param {string} className css
 * @param {string} contextMarginTop 
 * @param {string} contextMarginBottom 
 */
export class Header extends React.Component {

    //  css 
    static __DEFAULT_CLASS_NAME = 'w-100';
    // 
    static __DEFAULT_CONTEXT_MARGIN_TOP = '200px';
    // 
    static __DEFAULT_CONTEXT_MARGIN_BOTTOM = '200px';

    constructor(props) {

        // 
        super(props);

        // 
        this.state = {};

    }

    render() {

        // 
        return (
            <KLayout>
                <div>
                    <img
                        src={this.props.bgImg}
                        className={'position-fixed left-0 w-100 h-100 object-fit-cover'}
                        alt={'header-bg-img'}/>
                </div>
                <Row style={{
                    marginTop: this.props.contextMarginTop || Header.__DEFAULT_CONTEXT_MARGIN_TOP,
                    marginBottom: this.props.contextMarginBottom || Header.__DEFAULT_CONTEXT_MARGIN_BOTTOM
                }}>
                    <Col>
                        <div className={'text-align-center'}>
                            {this.props.children}
                        </div>
                    </Col>
                </Row>
            </KLayout>
        );

    }

}


